import React, { useEffect } from "react";
import styled from "styled-components";
import Staff1 from "../img/staff1.png";
import Staff2 from "../img/staff2.png";
import Staff3 from "../img/staff3.png";
import Staff4 from "../img/staff4.png";
import AOS from "aos";
import "aos/dist/aos.css";

const Title = styled.div`
  width: auto;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.333333px;
  color: #b0687b;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
`;

const Box = styled.div`
  width: 327px;
  height: 254px;
  border: 2px solid #e0c895;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
`;

const Text = styled.div`
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #5f4637;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  margin-bottom: 12px;
`;

const Color = styled.span`
  color: #8b2842;
`;

function InfoStaff() {
  useEffect(() => {
    AOS.init();
  });

  return (
    <>
      <Title> </Title>
      {/* 1*/}
      <div data-aos="zoom-in">
        <Box>
          <img
            src={Staff1}
            style={{
              width: "100%",
              height: "100%",
              objectFit: "cover",
              borderRadius: "3%",
            }}
            alt=""
          />
        </Box>
      </div>
      <div data-aos="zoom-in">
        <Text>
              
          <br /> <Color>   </Color> 
          <br /> , , , ,  .
        </Text>
      </div>
      {/* 2*/}
      <div data-aos="zoom-in">
        <Box>
          <img
            src={Staff2}
            style={{
              width: "100%",
              height: "100%",
              objectFit: "cover",
              borderRadius: "3%",
            }}
            alt=""
          />
        </Box>
      </div>
      <div data-aos="zoom-in">
        <Text>
             
          <br /> <Color>       </Color>
          
          <br />     ,
        </Text>
      </div>
      {/* 3*/}
      <div data-aos="zoom-in">
        <Box>
          <img
            src={Staff3}
            style={{
              width: "100%",
              height: "100%",
              objectFit: "cover",
              borderRadius: "3%",
            }}
            alt=""
          />
        </Box>
      </div>
      <div data-aos="zoom-in">
        <Text>
                ,
          <br />   ,
          <br />       
          <br /> <Color>  </Color> .
        </Text>
      </div>
      {/* 4*/}
      <div data-aos="zoom-in">
        <Box>
          <img
            src={Staff4}
            style={{
              width: "100%",
              height: "100%",
              objectFit: "cover",
              borderRadius: "3%",
            }}
            alt=""
          />
        </Box>
      </div>
      <div data-aos="zoom-in">
        <Text>
           <Color>  </Color>
          <br />        .
        </Text>
      </div>
    </>
  );
}

export default InfoStaff;
