import React, { useEffect } from "react";
import styled from "styled-components";
import Poster from "../img/poster.png";

import AOS from "aos";
import "aos/dist/aos.css";

const Title = styled.div`
  width: auto;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.333333px;
  color: #b0687b;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
`;

const PicDiv = styled.div`
  width: 327px;
  height: 460px;
  margin-left: auto;
  margin-right: auto;
`;

const TextDiv = styled.div`
  width: 375px;
  height: 208px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
`;

const Color = styled.span`
  color: #8b2842;
`;

const Color2 = styled.span`
  color: #b0687b;
`;

function InfoDDD() {
  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <>
      <Title> 2022  ..</Title>
      <div data-aos="zoom-in">
        <PicDiv>
          <img
            src={Poster}
            style={{
              width: "100%",
              height: "100%",
              objectFit: "cover",
            }}
            alt=""
          />
        </PicDiv>
      </div>

      <div data-aos="zoom-in">
        <TextDiv>
          2022  [<Color></Color>]
          <br />  <Color2></Color2> +  <Color2></Color2> +
            <Color2></Color2>   
          <br />
          <br />    
          <br /> <Color2>    </Color2>
          <br />    .
          <br />
          <br />        
          <br /> <Color2>    </Color2>
          <br /> <Color2> </Color2>  .
        </TextDiv>
      </div>
    </>
  );
}

export default InfoDDD;
